/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.header {
  height: 80px;
  background: #5358c1;
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 10;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo a {
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
}

.header__btn-open {
  width: 30px;
  height: 30px;
  display: none;
  position: relative;
  right: 0px;
}

.header__btn-open span,
.header__btn-open span:before,
.header__btn-open span:after {
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -1px;
}

.header__btn-open span:before,
.header__btn-open span:after {
  content: "";
  display: block;
  transition: all 0.2s;
}

.header__btn-open span:before {
  transform: translateY(-6px);
}

.header__btn-open span:after {
  transform: translateY(6px);
}

.header__btn-open span.active:before {
  transform: rotate(45deg);
}

.header__btn-open span.active:after {
  transform: rotate(-45deg);
}

.header__btn-open span.active {
  background-color: transparent;
}

@media (max-width: 900px) {
  .header__btn-open {
    display: block;
  }
}

@media (min-width: 901px) {
  .header__nav {
    display: block !important;
  }
}

@media (max-width: 900px) {
  .header__nav {
    position: absolute;
    background-color: grey;
    left: -100%;
    top: 80px;
    width: 100%;
    min-height: 420px;
    overflow-y: scroll;
    transition: all .5s;
    height: 100vh;
  }
}

@media (max-width: 900px) {
  .header__nav.open {
    left: 0;
  }
}

.header__nav-item {
  display: flex;
  font-weight: 600;
  align-items: center;
}

@media (max-width: 900px) {
  .header__nav-item {
    flex-direction: column;
  }
}

.header__nav-item li {
  padding: 8px 16px;
  border-radius: 10px;
  cursor: pointer;
}

.header__nav-item li a {
  color: #000000;
}

.header__nav-item li:first-child {
  background-color: #ffffff;
}

@media (max-width: 900px) {
  .header__nav-item li:first-child {
    margin-top: 30px;
  }
}

.header__nav-item li:first-child a {
  color: #4045BA;
}

.header__nav-item li:last-child {
  border: 1px solid #000000;
  background-color: #A549D0;
  width: 116px;
  line-height: 38px;
  text-align: center;
  padding: 0px;
  border-radius: 15px;
  transition: all .3s;
}

.header__nav-item li:last-child:hover {
  background-color: transparent;
}

.header__nav-item li:nth-child(2),
.header__nav-item li:nth-child(3),
.header__nav-item li:nth-child(4),
.header__nav-item li:nth-child(5) {
  margin-left: 35px;
}

@media (max-width: 900px) {
  .header__nav-item li:nth-child(2),
  .header__nav-item li:nth-child(3),
  .header__nav-item li:nth-child(4),
  .header__nav-item li:nth-child(5) {
    margin-left: 0px;
    margin-top: 20px;
  }
}

.header__nav-item li:nth-child(2) a,
.header__nav-item li:nth-child(3) a,
.header__nav-item li:nth-child(4) a,
.header__nav-item li:nth-child(5) a {
  color: #ffffff;
  transition: all .3s;
}

.header__nav-item li:nth-child(2),
.header__nav-item li:nth-child(3),
.header__nav-item li:nth-child(4) {
  transition: all .3s;
}

.header__nav-item li:nth-child(2):hover,
.header__nav-item li:nth-child(3):hover,
.header__nav-item li:nth-child(4):hover {
  background-color: #ffffff;
}

.header__nav-item li:nth-child(2):hover a,
.header__nav-item li:nth-child(3):hover a,
.header__nav-item li:nth-child(4):hover a {
  color: #4045BA;
  transition: all .3s;
}

.footer {
  background-color: #3438AC;
  padding-top: 56px;
}

.footer__content {
  background-color: #4045BA;
}

.footer__wrapper {
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 700px) {
  .footer__wrapper {
    flex-direction: column;
  }
}

.footer__item-left {
  display: flex;
  flex-direction: column;
  width: 35%;
}

@media (max-width: 700px) {
  .footer__item-left {
    width: 100%;
  }
}

.footer__item-subscribe {
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}

.footer__item-letter {
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  margin-top: 42px;
}

.footer__item-input {
  margin-top: 20px;
  position: relative;
  width: 100%;
}

@media (max-width: 700px) {
  .footer__item-input {
    max-width: 450px;
  }
}

.footer__item-email {
  border: none;
  outline: none;
  width: 100%;
  font-size: 14px;
  line-height: 48px;
  padding-left: 12px;
  padding-right: 40px;
  color: #ffffff;
  background-color: #3438AC;
  border-radius: 10px;
}

.footer__item-email::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.footer__item-email-btn {
  width: 15px;
  height: 15px;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translate(0, -50%);
}

.footer__item-email-btn:before {
  position: absolute;
  font-family: 'ntsame';
  content: "\e902";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.footer__item-checkbox {
  display: flex;
  align-items: center;
  margin-top: 16px;
}

.footer__item-checkbox-input {
  width: 24px;
  height: 24px;
  border-radius: 5px;
}

.footer__item-checkbox-text {
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  font-weight: 600;
  margin-left: 12px;
}

@media (max-width: 950px) {
  .footer__item-checkbox-text {
    max-width: 270px;
  }
}

